import React from 'react'
import { Button } from 'antd'
import CartItem from './CartItem'
import { useGlobalContext } from '../context'

const CartContainer = () => {
  const {
    state: { cart, total },
    clearCart
  } = useGlobalContext()
  if (cart.length === 0) {
    return (
      <section className="cart">
        {/* cart header */}
        <header>
          <h2>your bag</h2>
          <h4 className="empty-cart">is currently empty</h4>
        </header>
      </section>
    )
  }
  return (
    <section className="cart">
      {/* cart header */}
      <header>
        <h2>your bag</h2>
      </header>
      {/* cart items */}
      <div>
        {cart.map((item: any) => {
          return <CartItem key={item.id} {...item} />
        })}
      </div>
      {/* cart footer */}
      <footer>
        <hr />
        <div className="cart-total">
          <h4>
            total <span>{total}</span>
          </h4>
        </div>
        <Button type="primary" onClick={clearCart} size="large">
          clear cart
        </Button>
      </footer>
    </section>
  )
}

export default CartContainer
